body {
    background-color: #123;
    color: #fff;
    font-family: monospace;
}
main {
    width: 300px;
    margin: 50px auto 0;
}
div.board {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    border: 2px solid #fff;
}
div.square {
    width: 100px;
    height: 100px;
    border: 2px solid #fff;
    box-sizing: border-box;
    cursor: pointer;
    font-size: 4rem;
    text-align: center;
}
div.result {
    font-size: 2rem;
    padding: 10px;
}
div.result.green {
    background-color: #5a5;
}
div.result.red {
    background-color: #a55;
}